import React from "react";
import App from "./src/containers/App";
import { Grommet } from "grommet";

const theme = {
  tab: {
    active: "red",
  },
  global: {
    font: {
      family: "Roboto",
      size: "18px",
      height: "20px",
    },
  },
};

export const wrapRootElement = ({ element }) => {
  return (
    <Grommet theme={theme}>
      <App>{element}</App>
    </Grommet>
  );
};
